import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import Vector from './utils/vector';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

class Dot {
  constructor({ x, y }, speed = -(1 / 1000)) {
    this.position = new Vector({ x, y });
    this.velocity = new Vector({ x: speed, y: 0 });
  }

  update() {
    this.position.add(this.velocity);
  }

  get x() {
    return this.position.x;
  }

  get y() {
    return this.position.y;
  }
}

class Line {
  constructor(segments) {
    this.path = this._createPath(segments);
    this.pinch = (1 / this.path.length);
  }

  _createPath(segments) {
    this.speed = -(Math.random() / 1000);
    return new Array(segments).fill(0).map((v, idx) => new Dot({
      x: (idx / (segments - 1)),
      y: idx % 2 ? 0.25 - (Math.random() / 4) : 0.75 + (Math.random() / 4)
    }, this.speed))
  }

  update(frame) {
    let marked = false;

    for (let i = 0; i < this.path.length; i++) {
      this.path[i].update();

      if (this.speed < 0 && this.path[i].x < 0) {
        marked = i;
      }

      if (this.speed > 0 && this.path[i].x >= 1) {
        marked = i;
      }
    }

    if (marked) {
      const cut = this.path.splice(marked, 1);

      if (this.speed < 0) {
        cut[0].position.x = 1;
        this.path.push(cut[0]);
      }

      if (this.speed > 0) {
        cut[0].position.x = 0;
        this.path.unshift(cut[0]);
      }
    }
  }

  debugRender(width, height) {
    for (let i = 0; i < this.path.length; i++) {
      ctx.fillRect(
        this.getX(i, width), this.getY(i, height),
        5, 5
      );
    }
  }

  getX(idx, width) {
    return this.path[idx].x * width;
  }

  getY(idx, height) {
    return this.path[idx].y * height;
  }

  render(width, height) {
    ctx.beginPath();
    ctx.moveTo(this.getX(0, width), this.getY(0, height));
    for (let i = 1; i < this.path.length; i++) {
      let x = this.getX(i, width);
      let y = this.getY(i, height);

      ctx.bezierCurveTo(
        this.getX(i - 1, width) + (width * this.pinch), this.getY(i - 1, height),
        x - (width * this.pinch), y,
        this.getX(i, width), this.getY(i, height)
      );
    }
    ctx.lineTo(width, height);
    ctx.lineTo(0, height);
    ctx.closePath();
    ctx.fill();
  }
}

const lines = [
  new Line(11),
  new Line(5),
  new Line(21),
  new Line(7),
];

const colours = [
  ['#ff5722', '#1976d2'],
  ['#1a237e', '#ef1717'],
  ['#ffe200', 'green'],
  ['#1976d2', '#1a237e'],
]

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);

  let slice = height / lines.length;
  for (let i = 0; i < lines.length; i++) {
    ctx.save();
    lines[i].update();
    ctx.translate(-(width * 0.5), slice * i);
    ctx.fillStyle = colours[i][0];
    ctx.fillRect(0, 0, width * 2, slice);
    ctx.fillStyle = colours[i][1];
    lines[i].render(width * 2, slice);
    ctx.restore();
  }
});

app.start();
